import * as React from 'react';
import { ContentPage } from '@pidoc/components';

    import { PageContent, Markdown, PageLayout } from '@pidoc/components';

    const link = "https://github.com/smapiot/pidoc/tree/develop/packages/example-app/docs/generators/90-custom.md";
    const html = `
<h1 id="custom-generators" tabindex="-1">Custom Generators</h1>
<p>A custom generator transforms files according to your rules and logic. This can be used to teach pidoc about a new file type (e.g., transforming ASCIIDoc or similar).</p>
<p>(tbd)</p>
<p>Let's say you have a folder structure like this:</p>
<pre><code class="hljs language-plain">docs/
docs/foo/content.ascii
docs.config.json
</code></pre>
<p>A sitemap section in <em>docs.config.json</em> to cover this would be:</p>
<pre><code class="hljs language-json"><span class="hljs-punctuation">{</span>
  <span class="hljs-comment">// ...</span>
  <span class="hljs-attr">&quot;sitemap&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-punctuation">{</span>
    <span class="hljs-attr">&quot;example&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-punctuation">{</span>
      <span class="hljs-attr">&quot;title&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-string">&quot;Example&quot;</span><span class="hljs-punctuation">,</span>
      <span class="hljs-attr">&quot;sections&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-punctuation">[</span>
        <span class="hljs-punctuation">{</span>
          <span class="hljs-attr">&quot;generator&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-string">&quot;custom&quot;</span><span class="hljs-punctuation">,</span>
          <span class="hljs-attr">&quot;path&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-string">&quot;/path/to/custom/generator.js&quot;</span><span class="hljs-punctuation">,</span>
          <span class="hljs-attr">&quot;segment&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-string">&quot;&quot;</span><span class="hljs-punctuation">,</span>
          <span class="hljs-attr">&quot;dir&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-string">&quot;foo&quot;</span>
        <span class="hljs-punctuation">}</span>
      <span class="hljs-punctuation">]</span>
    <span class="hljs-punctuation">}</span>
  <span class="hljs-punctuation">}</span>
<span class="hljs-punctuation">}</span>
</code></pre>
<p>This will look up the <em>.ascii</em> files in the <code>foo</code> directory below the <code>docs</code> documentation root directory.</p>
<div class="box tip"><p class="box-title">Use packages</p>
<p>Custom generators can also be deployed in npm packages. In this case you can reference them via their package name, e.g.:</p>
<pre><code class="hljs language-json"><span class="hljs-punctuation">{</span>
  <span class="hljs-comment">// ...</span>
  <span class="hljs-attr">&quot;sitemap&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-punctuation">{</span>
    <span class="hljs-attr">&quot;example&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-punctuation">{</span>
      <span class="hljs-attr">&quot;title&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-string">&quot;Example&quot;</span><span class="hljs-punctuation">,</span>
      <span class="hljs-attr">&quot;sections&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-punctuation">[</span>
        <span class="hljs-punctuation">{</span>
          <span class="hljs-attr">&quot;generator&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-string">&quot;custom&quot;</span><span class="hljs-punctuation">,</span>
          <span class="hljs-attr">&quot;path&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-string">&quot;name-of-custom-generator-package&quot;</span><span class="hljs-punctuation">,</span>
          <span class="hljs-attr">&quot;segment&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-string">&quot;&quot;</span><span class="hljs-punctuation">,</span>
          <span class="hljs-attr">&quot;dir&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-string">&quot;foo&quot;</span>
        <span class="hljs-punctuation">}</span>
      <span class="hljs-punctuation">]</span>
    <span class="hljs-punctuation">}</span>
  <span class="hljs-punctuation">}</span>
<span class="hljs-punctuation">}</span>
</code></pre>
<p>where <code>name-of-custom-generator-package</code> is the name of the package.</p>
</div>
`;
    const meta = {"title":"Custom","description":"Information about providing a custom generator.","audience":"Everyone","level":"Beginner","section":"Generators"};
  

export default () => (
  <ContentPage>
    
    <PageLayout name={"default"} meta={meta}>
      <PageContent meta={meta}>
        <Markdown content={html} link={link} editLabel={"Edit on GitHub"} />
      </PageContent>
    </PageLayout>
  
  </ContentPage>
);